import { useContext } from "react"
import { AppContext } from './../App';


export const Info = ({ image, title, description }) => {

	const { setCartOpened } = useContext(AppContext);

	return (
		<div className="cartEmpty d-flex align-center justify-center flex-column">
			<img className="mb-20" width={120} src={image} alt="Empty Cart" />
			<h2>{title}</h2>
			<p className="opacity-6 text-center">{description}</p>
			<button onClick={() => setCartOpened(false)} className="greenButton">
				<img className="pr-10" src="img/arrow.svg" alt="Arrow" />
				Вернуться назад
			</button>
		</div>
	)
}
